  <div id="app" class="d-flex flex-column h-100">
    <nav :class='{"fixed-top":  $route.path === "/", "navbar-light":  $route.path !== "/",  "border-bottom":  $route.path !== "/"}' class="navbar navbar-expand-lg navbar-light py-3" :id=" $route.path != '/' ? 'secondNav' : 'mainNav'">
      <div class="container px-4 px-lg-5">
        <a class="navbar-brand" href="/">
          <img src="./assets/logo.png" alt="logo" width="70" height="40" />
          <span class="fs-4">ПЭС</span></a
        >
        <button
          class="navbar-toggler navbar-toggler-right"
          type="button"
          @click='showNavbar = !showNavbar'
        >
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" :class='{"show": showNavbar}' id="navbarResponsive">
          <ul class="navbar-nav ms-auto my-2 my-lg-0">
            <li class="nav-item border-lg-bottom">
              <a href="/" class="nav-link" @click='showNavbar = false' :class="{ active: $route.path == '/' }">Главная</a>
            </li>
            <li class="nav-item  border-lg-bottom" >
              <a class="nav-link"  @click='showNavbar = false' href="#about">О компании</a>
            </li>

            <li class="nav-item  border-lg-bottom" >
              <a class="nav-link" @click='showNavbar = false' href="#services">Услуги</a>
            </li>
            <li class="nav-item  border-lg-bottom" >
              <a class="nav-link" @click='showNavbar = false' href="#contact">Контакты</a>
            </li>
            <li class="nav-item  border-lg-bottom">
              <a class="nav-link" @click='showNavbar = false' href="/pesprofen#/news">Новости компании</a>
            </li>

            <li class="nav-item  border-lg-bottom">
              <a class="nav-link" :class="{ active: $route.path == '/#/examples' }" href="/pesprofen#/examples"
                >Примеры работ</a
              >
            </li>
          </ul>
        </div>
      </div>
    </nav>
    <router-view/>

    <!-- Footer-->
    <footer class="bg-light py-5 mt-auto">
      <div class="container px-4 px-lg-5">
